import React from 'react';
import { useState, useEffect } from 'react';
import contactsService from './services/contacts';
import loginService from './services/login';
import './App.css';
import ContactDisplay from './components/ContactDisplay';
import ErrorDisplay from './components/ErrorDisplay';

function App() {
  const [persons, setPersons] = useState([]);
  const [newFirstName, setNewFirstName] = useState('');
  const [newLastName, setNewLastName] = useState('');
  const [newPhoneNumber, setNewPhoneNumber] = useState('');
  const [filterBy, setFilterBy] = useState('');
  const [error, setError] = useState(null);
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [user, setUser] = useState(null);

  useEffect(() => {
    contactsService.getAll()
                   .then(persons => setPersons(persons));
  }, []);

  useEffect(() => {
    const loggedInUserJSON = window.localStorage.getItem('loggedInContactsUser');
    if (loggedInUserJSON) {
      const user = JSON.parse(loggedInUserJSON);
      setUser(user);
      contactsService.setToken(user.token);
    }
  }, []);

  const alreadyExists = (newPerson) => {
    return (persons.some(person => {
             return ( person.firstName.toLowerCase() === newPerson.firstName.toLowerCase() &&
                      person.lastName.toLowerCase() === newPerson.lastName.toLowerCase() )
    }));
  }

  const handleSubmit = (event) => {
    event.preventDefault();

    let newPerson = {
      firstName: newFirstName,
      lastName: newLastName,
      phoneNumber: newPhoneNumber,
    }

    if (alreadyExists(newPerson)) {
      return alert(`"${newFirstName} ${newLastName}" already exists!`);
    }

    contactsService.create(newPerson)
                   .then(person => {
                       setPersons(persons.concat(person));
                       setNewFirstName('');
                       setNewLastName('');
                       setNewPhoneNumber('');
                   })
                  .catch(err => {
                    setError(err);
                    setTimeout(setError.bind(null, null), 4000);
                  });
  }

  const handleFirstNameInput = (event) => {
    let newFirstName = event.target.value.trim();
    setNewFirstName(newFirstName);
  }

  const handleLastNameInput = (event) => {
    let newLastName = event.target.value.trim();
    setNewLastName(newLastName);
  }

  const handlePhoneNumberInput = (event) => {
    let newPhoneNumber = event.target.value;
    setNewPhoneNumber(newPhoneNumber);
  }

  const handleSearchInput = (event) => {
    let query = event.target.value;
    setFilterBy(query);
  }

  let personsToShow = persons.filter(person => {
    return `${person.firstName.toLowerCase()} ${person.lastName.toLowerCase()}`.includes(filterBy.toLocaleLowerCase())
  });

  const handleLogin = async (e) => {
    e.preventDefault();
    try {
      const user = await loginService.login({ username, password });
      window.localStorage.setItem('loggedInContactsUser', JSON.stringify(user));

      contactsService.setToken(user.token);
      setUser(user);
      setUsername('');
      setPassword('');
    } catch (err) {
      setError(err);
      setTimeout(() => {
        setError(null)
      }, 5000)
    }
  }

  const handleLogout = (e) => {
    window.localStorage.removeItem('loggedInContactsUser');
    setUser(null);
  }

  const loginForm = () => {
    return (
      <form onSubmit={handleLogin}>
        <div>
          Username
          <input type='text' value={username} name='Username' onChange={(e) => setUsername(e.target.value)} />
        </div>
        <div>
          Password
          <input type='text' value={password} name='Password' onChange={(e) => setPassword(e.target.value)} />
        </div>
        <button type='submit'>Login</button>
      </form>
    )
  }

  const contactForm = () => {
    return (
      <>
        <h2>New Contact</h2>
        <form onSubmit={handleSubmit}>
          <div>
            First Name: <input id='firstNameInput' type='text' onChange={handleFirstNameInput} value={newFirstName}/>
          </div>
          <div>
            Last Name: <input id='lastNameInput' type='text' onChange={handleLastNameInput} value={newLastName}/>
          </div>
          <div>
            Phone Number: <input id='phoneNumberInput' type='text' onChange={handlePhoneNumberInput} value={newPhoneNumber}/>
          </div>
          <div>
            <button type='submit'>Submit</button>
          </div>
        </form>
      </>
    )
  }

  return (
    <div className="App">
      <h1>Contacts</h1>
      <ErrorDisplay error={error}></ErrorDisplay>
      {user === null ? 
      loginForm() : 
      <>
        <p>Logged in as {user.name}</p>
        <button onClick={handleLogout}>Log Out</button>
        {contactForm()}
        <div>
        Search: <input id='searchBar' type='text' onChange={handleSearchInput} value={filterBy}></input>
        </div>
        <ContactDisplay allPersons={persons} personsToShow={personsToShow} setPersons={setPersons} error={error} setError={setError}></ContactDisplay>
      </>
      }
    </div>
  );
}

export default App;
